nav {
    height: 10vh;
    width: 100%;
    background: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 50px;
    font-size: 30px;
    box-shadow: 0 1px 4px #353c3b ;
    position: fixed;
    top: 0;
    z-index: 1;
}
nav img {
    margin-right: 450px;
}
nav > img{
    width: 20rem;
}
nav a {
    text-decoration: none;
    color: white;
    font-size: 20px;
    font-weight: 550;
    position: relative;
}
nav a::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #93d5ca;
    bottom: -5px;
    left: 0;
    opacity: 0;
    transition: opacity 0.4s ease-in-out;
}
nav a:hover::after {
    opacity: 1;
    color: #00cdac;
}
nav a:hover {
    text-shadow: 1.5px 1.5px #00e9c2;
    color: #fff;
} 

@media only screen and (max-width: 600px) {
nav {
    align-items: center;
}
nav > img {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px;
}
nav > a {
    display: none;
}
#avatar{
    display: none;
}
}